import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, Content  } from 'ionic-angular';
import { UserService } from '../../service/user.service'
import { 
	ActionSheetController ,
	AlertController,
	ModalController,
	PopoverController,
  LoadingController
} from 'ionic-angular';

//import { AlertPage } from '../alert/alert'
import { BtnPage } from '../btns/btns'
import { CardPage } from '../card/card';
import { ModalPage } from '../modal/modal';
import { RadioPage } from '../radio/radio';

@IonicPage({
  name:'home',
  segment:'home'
})
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController,
  	public actionSheetCtrl: ActionSheetController,
  	public modalCtrl: ModalController,
  	public popoverCtrl: PopoverController,
  	public alertCtrl: AlertController,
    public userSer:UserService,
    public loadingCtrl: LoadingController) {
    //this.displayLoading()
    /*测试service*/
    console.log(this.userSer.users[0]);
    this.userSer.users=[{name:'normal'}]
  }
  @ViewChild(Content) 
  content: Content;
  /*加载框*/ 
  displayLoading() {
    let loading = this.loadingCtrl.create({
      content: `
        <div class="custom-spinner-container">
          <div class="custom-spinner-box">
          虚拟的盒子</div>
        </div>`,
      duration: 1000
    });

    loading.onDidDismiss(() => {
      console.log('Dismissed loading');
    });

    loading.present();
  }
  public  event = {
    month: '1990-02-19',
    timeStarts: '07:43',
    timeEnds: '1990-02-20'
  }
  public RadioPage=RadioPage;
  navToBtns() {
  	this.navCtrl.push(BtnPage)
  }
  navToAlert() {
  	this.navCtrl.push('AlertPage',{id:1})
  }
  navToCard() {
  	this.navCtrl.push(CardPage)
  }
  openPage(page) {
  	this.navCtrl.push(page)
  }
  openModal() {
  	let modal = this.modalCtrl.create(ModalPage);
    modal.present();
  }
  openPopover() {
    let popover = this.popoverCtrl.create(ModalPage);
    popover.present();
  }

  ionViewDidLoad(){
        console.log('home加载');
    }
  
}



